<template>
  <div class="study">
    <v-container>
      <v-row>
        <v-col>
          <h1 class="text-center mt-10"> 1</h1>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-form ref="form" v-model="valid" class="bg-white pa-10 rounded-lg" @submit.prevent>
            <v-text-field
              v-model="studyTitle"
              :rules="[rules.required]"
              label=""
              required
              variant="outlined"
              class="mb-3"
            ></v-text-field>
            <v-text-field
              v-model="keywords"
              :rules="[rules.required]"
              label=""
              required
              variant="outlined"
              class="mb-3"
            ></v-text-field>
            <v-textarea
              v-model="studyContent"
              :rules="[rules.required]"
              label=" "
              required
              variant="outlined"
              class="mb-3"
            ></v-textarea>
            
            <!-- key       -->
            <div class="file-input-container mb-3">
              <v-file-input 
                :key="fileInputKey"
                v-model="studyFile"
                label="" 
                variant="outlined"
                prepend-icon="mdi-paperclip"
              ></v-file-input>
            </div>
            
            <v-row justify="center" class="mt-4">
              <v-btn
                class="mr-3"
                :disabled="!valid"
                color="success"
                @click="submitStudy"
              >
                
              </v-btn>
              <v-btn
                color="error"
                @click="manualResetForm"
              >
                
              </v-btn>
            </v-row>
          </v-form>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { auth, db, storage } from '@/firebase'; 
import { onAuthStateChanged } from 'firebase/auth';
import { collection, addDoc, serverTimestamp } from 'firebase/firestore';
import { ref as storageRef, uploadBytes, getDownloadURL } from 'firebase/storage';

//   
const form = ref(null);
const valid = ref(false);
const studyTitle = ref('');
const keywords = ref('');
const studyContent = ref('');
const studyFile = ref(null);
const user = ref(null);
//       
const fileInputKey = ref(0);

//   
const rules = {
  required: value => !!value || '  .'
};

//  
const submitStudy = async () => {
  if (!form.value || !form.value.validate()) return;
  
  try {
    let fileUrl = null;
    
    //     Storage 
    if (studyFile.value) {
      const timestamp = new Date().getTime();
      const fileName = studyFile.value.name;
      //     
      const encodedFileName = encodeURIComponent(`${timestamp}_${fileName}`);
      const fileRef = storageRef(storage, `study_files/${encodedFileName}`);
      
      console.log(' :', studyFile.value);
      console.log(' :', `study_files/${encodedFileName}`);
      
      try {
        const snapshot = await uploadBytes(fileRef, studyFile.value);
        console.log('  :', snapshot);
        fileUrl = await getDownloadURL(snapshot.ref);
        console.log(' URL  :', fileUrl);
      } catch (uploadError) {
        console.error('  :', uploadError);
        if (!confirm('  .    ?')) {
          throw new Error(' ');
        }
      }
    }
    
    // Firestore  
    const postData = {
      title: studyTitle.value,
      author: user.value ? user.value.email : '',
      createAt: serverTimestamp(),
      tags: keywords.value.split(',').map(tag => tag.trim()),
      body: studyContent.value,
      file: fileUrl
    };
    
    console.log('Firestore  :', postData);
    await addDoc(collection(db, 'posts'), postData);
    
    alert(' .');
    manualResetForm();
  } catch (error) {
    console.error('Error submitting post:', error);
    if (error.message !== ' ') {
      alert(' .  .');
    }
  }
};

//     (form.reset()  )
const manualResetForm = () => {
  //    
  studyTitle.value = '';
  keywords.value = '';
  studyContent.value = '';
  
  //      key 
  studyFile.value = null;
  fileInputKey.value += 1;  // key    
  
  //   
  valid.value = false;
};

// Firebase   
const initFirebaseAuth = () => {
  onAuthStateChanged(auth, (authUser) => {
    if (authUser) {
      user.value = authUser;
    } else {
      user.value = null;
    }
  });
};

//     
onMounted(() => {
  initFirebaseAuth();
});
</script>

<style scoped lang="scss">
.study .v-form {
  margin-bottom: 100px !important;
}

.file-input-container {
  width: 100%;
}
</style>

